<template>

    <div v-if="confirm"
         class="agreement-result">

        <div class="agreement-result-icon">
            <span class="icon-double-checkmark"></span>
        </div>

        <div class="h3">Спасибо!</div>

        <p>Ваше согласие успешно отправлено!</p>

    </div>

    <div v-else
         class="agreement-result danger">

        <div class="agreement-result-icon">
            <span class="icon-close-2"></span>
        </div>

        <div class="h3">Отклонено!</div>

        <p>Ваш отказ принят!</p>

    </div>

</template>

<script>
    import {mapState} from "vuex";

    export default {
        name: "AgreementResult",
        computed: {
            ...mapState({
                confirm: state => state.agreement.confirm
            })
        }
    }
</script>

